import React,{useState,useEffect, Fragment, useContext} from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import css from '../read/read.module.css'
import Spinner from '../spinner/Spinner';
import {useDispatch} from 'react-redux'
import { actionCreators } from '../state/index'
import NoData from '../read/NoData';
import { AuthContextApi } from '../contextApi/AuthContextapi';
import { toast } from 'react-toastify';
import swal from 'sweetalert';


const UserHome = () => {

    // const {UPbranch, UPsem}= useParams();
    const UPbranch = localStorage.getItem("Branch");
    const UPsem = localStorage.getItem("sem");


    const dispatch = useDispatch();

    const context = useContext(AuthContextApi);
    const {logOut} = context;

    const apiKey = process.env.REACT_APP_API_KEY;
    const refreshApiKey = process.env.REACT_APP_ADMIN_REFRESH_API_KEY;
    const EnvPageSize = Number(process.env.REACT_APP_PAGE_SIZE);

    const [trigger, setTrigger] = useState(false);

    const [studList, setstudList] = useState([]);
    const [searchUSN, setSearchUSN] = useState("");
    const [pageSize, setPagesize] = useState(EnvPageSize);

    const [hide, setHide] = useState(true);

    const [loading, setLoading] = useState(false);

    const [pageHeight, setPageHeight] = useState(0);
    const [newPageHeight, setNewPageHeight] = useState(0)

    const [accessToken, setAccessToken] = useState("");

    const getAccessToken = () => {
        
        const refresh_token = localStorage.getItem('JWT_REFRESH_KEY');
        if(refresh_token!==null){
            axios({ 
                method: 'GET',
                url: `${refreshApiKey}/api/auth/refresh`,
                headers: {
                    "Authorization": 'Bearer '+refresh_token
                }
            })
                .then((response)=>{
                setAccessToken(response.data.accessToken);
            })
            .catch((error)=>{
                console.log(error);
                logOut();
            });
        }

    }

    const getData = () =>{

        if(UPbranch!==null && searchUSN===''){
            getSpecialData();
        }
        else{
            setLoading(true)
            if(accessToken!==""){
                axios({
                    method: 'GET',
                    url: `${apiKey}/api/students${searchUSN ? `/${searchUSN}` :`rest?page=0&size=${pageSize}`}`,
                    headers: {
                        "Authorization": 'Bearer '+ accessToken
                    }
                })    
                .then((response)=>{
                    // console.log(response);
                    setLoading(false)
                    {searchUSN ? setstudList(response.data) : setstudList(response.data._embedded.studentList)}
                })
                .catch((error)=>{
                    console.log(error);
                    setLoading(false)
                });
            }
        }
    }
    const getSpecialData = () =>{
        setLoading(true)
        if(accessToken!==""){
            axios({
                method: 'GET',
                url: `${apiKey}/api/students/${UPbranch}/${UPsem}/`,
                headers: {
                    "Authorization": 'Bearer '+ accessToken
                }
            })    
            .then((response)=>{
                setLoading(false)
                setstudList(response.data);
            })
            .catch((error)=>{
                console.log(error);
                setLoading(false)
            });
        }
    }

    const handleScroll = (e) => {
        // console.log('top:', e.target.documentElement.scrollTop);
        // console.log('height:', e.target.documentElement.scrollHeight);
        // console.log('windo:', window.innerHeight);
        let hold =e.target.documentElement.scrollHeight;

        setNewPageHeight(hold);

        if((e.target.documentElement.scrollTop + window.innerHeight +1 )>= hold ){
            setPagesize((oldpagesize)=> oldpagesize+5);
            setPageHeight(hold);
        }
    }

    useEffect(() => {
        getAccessToken();
        window.addEventListener('scroll',handleScroll);
        dispatch(actionCreators.setName(undefined));

        return()=>{
            setstudList([])
        }
    }, [])

    useEffect(() => {
        if(pageHeight >= newPageHeight) return;
        getAccessToken();
    }, [pageSize]);

    useEffect(() => {
        if(UPbranch===null &&UPsem===null){
            getData();
        }
        else{
            getSpecialData();
        }
      setTrigger(false);
    }, [accessToken,trigger]);

    const sendData = (id) => {
        localStorage.setItem("ID",id);
    }

    // const deleteData = (delete_id, delete_name, delete_usn) => {

    //     if(accessToken!==""){
    //         swal(`Enter ${delete_name} Usn to Confirm Delete`, {
    //             content: "input",
    //           })
    //           .then((value) => {
    //               if(value===delete_usn){
    //                     axios({
    //                         method: 'DELETE',
    //                         url:`${apiKey}/api/students/${delete_id}`,
    //                         headers: {
    //                             "Authorization": 'Bearer '+ accessToken
    //                         }
    //                     }) 
    //                     .then(()=>{
    //                         toast.success('Student deleted successfully');
    //                         setTrigger(true);
    //                     })
    //                     .catch((error)=>{
    //                         toast.error("Invalid student usn")
    //                         console.log(error);
    //                     });
    //                 }
    //                 else if(value===''){
    //                     deleteData(delete_id, delete_name, delete_usn);
    //                 }
    //                 else{
    //                    toast.error('Invalid Usn')
    //                 } 
    //          });

    //     }
    // }
    
    let i=1;

    const Show = (props) => {
        let {data} = props;
        return(
            <>
                <div className={css.for3Btn}>
                    <div>
                        <Link to="/userfeedetail" >
                            <button onClick={()=> sendData(data.id)} className={css.FeeDetBtn}><i className="fas fa-rupee-sign"></i>&ensp;Fee Detail</button>
                        </Link>
                    </div>
                </div>
            </>
        )
    }

    // let Hidden = () => {
    //     return (
    //         <Fragment>
    //             <div>
    //                 <i className="fas fa-edit" onClick={()=>{setHide(false)}} style={{cursor:'pointer'}}></i>
    //             </div>
    //         </Fragment>
    //     )
    // }

    return (
        <div className={css.container}>
            <div className={css.subContainer}>
               <div className={css.searchDiv} >
                    <div className={css.inputMain}>
                         <label htmlFor="search" className={css.label}>Search Sudent</label>
                         <input type="search" id="search" placeholder="Enter usn" onChange={(e)=>{setSearchUSN(e.target.value)}} className={css.input} />
                    </div>
                    <div>
                        <button onClick={getData} className={css.searchBtn}>Search</button>
                    </div>
               </div>
                {/* <div className={css.addStudDiv}>
                    <Link to="/create">
                            <button className={css.addBtn}><i className="fas fa-user-plus" style={{color:'white'}}></i>&ensp;Add Student</button>
                    </Link>
                </div> */}

           </div>
           <div className={css.subContainer2}>
                <table className={css.table} >
                    <thead className={css.thead}>
                        <tr>
                            <th>SI</th>
                            <th>Name</th>
                            <th>Usn</th>
                            <th>Department</th>
                            <th>Semester</th>
                            <th>Fee</th>
                        </tr>
                    </thead>

                            {
                                studList.map((data, key) => {return (
                                        <tbody key={key} className={css.tbody}>
                                            <tr className={css.tr}>
                                                <td>{i++}</td>
                                                <td>{data.name}</td>
                                                <td>{data.usn}</td>
                                                <td>{data.department}</td>
                                                <td>{data.sem}</td>
                                                <td> <Show data={data} /> </td>
                                            </tr>
                                        </tbody>
                                )})
                            }
                </table>
            </div>  
            {loading ? <Spinner/> : studList.length===0 && <NoData/>}
        </div>
    )
}

export default UserHome;
