import React ,{useState, useEffect} from 'react'
import axios from 'axios'
import './scss/branchDetails.scss'
import BrachesCard from './BrachesCard';

const BranchDetails = () => {
    const [students, setStudents] = useState([]);

    const apiKey = process.env.REACT_APP_API_KEY;

    const [cse, setCse] = useState({
            "sem1":0,
            "sem2":0,
            "sem3":0,
            "sem4":0,
            "sem5":0,
            "sem6":0,
            "sem7":0,
            "sem8":0,
            "total":0
     });
    const [ise, setIse] = useState({
            "sem1":0,
            "sem2":0,
            "sem3":0,
            "sem4":0,
            "sem5":0,
            "sem6":0,
            "sem7":0,
            "sem8":0,
            "total":0
     });

    const [me, setMe] = useState({
            "sem1":0,
            "sem2":0,
            "sem3":0,
            "sem4":0,
            "sem5":0,
            "sem6":0,
            "sem7":0,
            "sem8":0,
            "total":0
     });

     const [ce, setCe] = useState({
        "sem1":0,
        "sem2":0,
        "sem3":0,
        "sem4":0,
        "sem5":0,
        "sem6":0,
        "sem7":0,
        "sem8":0,
        "total":0
 });
     const [mte, setMte] = useState({
        "sem1":0,
        "sem2":0,
        "sem3":0,
        "sem4":0,
        "sem5":0,
        "sem6":0,
        "sem7":0,
        "sem8":0,
        "total":0
 });
     const [ae, setAe] = useState({
        "sem1":0,
        "sem2":0,
        "sem3":0,
        "sem4":0,
        "sem5":0,
        "sem6":0,
        "sem7":0,
        "sem8":0,
        "total":0
 });
     const [ece, setEce] = useState({
        "sem1":0,
        "sem2":0,
        "sem3":0,
        "sem4":0,
        "sem5":0,
        "sem6":0,
        "sem7":0,
        "sem8":0,
        "total":0
 });


    useEffect(() => {
          fetchUser();

          return(()=>{
              setStudents(null);
          })
    }, []);


    const fetchUser= () => {
        let webToken = localStorage.getItem('JWT_ACCESS_KEY');
        axios(
        {
            method:'GET',
            url:`${apiKey}/api/students`,
            headers: {
                'Authorization' : "Bearer "+webToken
            }
        })
        .then((response)=>{
            setStudents(response.data);
        })
        .catch((error)=>{
            console.log(error);
        })
    }

    useEffect(()=>{

        if(students.length!==0){
            students.filter(f =>{
                if(f.department==='CSE'){
                    setCse({...cse,"total":++cse.total})
                    switch(f.sem){
                        case "1": setCse({...cse, "sem1":++cse.sem1});break;
                        case "2": setCse({...cse, "sem2":++cse.sem2});break;
                        case "3": setCse({...cse, "sem3":++cse.sem3});break;
                        case "4": setCse({...cse, "sem4":++cse.sem4});break;
                        case "5": setCse({...cse, "sem5":++cse.sem5});break;
                        case "6": setCse({...cse, "sem6":++cse.sem6});break;
                        case "7": setCse({...cse, "sem7":++cse.sem7});break;
                        case "8": setCse({...cse, "sem8":++cse.sem8});break;
                        default: setCe();
                    }
                }
                else if(f.department==='ISE'){
                    setIse({...ise, "total":++ise.total});
                    switch(f.sem){
                        case "1": setIse({...ise, "sem1":++ise.sem1});break;
                        case "2": setIse({...ise, "sem2":++ise.sem2});break;
                        case "3": setIse({...ise, "sem3":++ise.sem3});break;
                        case "4": setIse({...ise, "sem4":++ise.sem4});break;
                        case "5": setIse({...ise, "sem5":++ise.sem5});break;
                        case "6": setIse({...ise, "sem6":++ise.sem6});break;
                        case "7": setIse({...ise, "sem7":++ise.sem7});break;
                        case "8": setIse({...ise, "sem8":++ise.sem8});break;
                        default:setIse();
                    } 
                }
               else if(f.department==='ME'){   
                setMe({...me, "total":++me.total});
                    switch(f.sem){
                        case "1": setMe({...me, "sem1":++me.sem1});break;
                        case "2": setMe({...me, "sem2":++me.sem2});break;
                        case "3": setMe({...me, "sem3":++me.sem3});break;
                        case "4": setMe({...me, "sem4":++me.sem4});break;
                        case "5": setMe({...me, "sem5":++me.sem5});break;
                        case "6": setMe({...me, "sem6":++me.sem6});break;
                        case "7": setMe({...me, "sem7":++me.sem7});break;
                        case "8": setMe({...me, "sem8":++me.sem8});break;
                        default: setMe();
                    } 
                }
                else if(f.department==='CE'){
                    setCe({...ce, "total":++ce.total});
                    switch(f.sem){
                        case "1": setCe({...ce, "sem1":++ce.sem1});break;
                        case "2": setCe({...ce, "sem2":++ce.sem2});break;
                        case "3": setCe({...ce, "sem3":++ce.sem3});break;
                        case "4": setCe({...ce, "sem4":++ce.sem4});break;
                        case "5": setCe({...ce, "sem5":++ce.sem5});break;
                        case "6": setCe({...ce, "sem6":++ce.sem6});break;
                        case "7": setCe({...ce, "sem7":++ce.sem7});break;
                        case "8": setCe({...ce, "sem8":++ce.sem8});break;
                        default:setCe();
                    } 
                }
                else if(f.department==='MTE'){
                    setMte({...mte, "total":++mte.total})
                    switch(f.sem){
                        case "1": setMte({...mte, "sem1":++mte.sem1});break;
                        case "2": setMte({...mte, "sem2":++mte.sem2});break;
                        case "3": setMte({...mte, "sem3":++mte.sem3});break;
                        case "4": setMte({...mte, "sem4":++mte.sem4});break;
                        case "5": setMte({...mte, "sem5":++mte.sem5});break;
                        case "6": setMte({...mte, "sem6":++mte.sem6});break;
                        case "7": setMte({...mte, "sem7":++mte.sem7});break;
                        case "8": setMte({...mte, "sem8":++mte.sem8});break;
                        default:setMte();
                    } 
                }
                else if(f.department==='AE'){
                    setAe({...ae, "total":++ae.total})
                    switch(f.sem){
                        case "1": setAe({...ae, "sem1":++ae.sem1});break;
                        case "2": setAe({...ae, "sem2":++ae.sem2});break;
                        case "3": setAe({...ae, "sem3":++ae.sem3});break;
                        case "4": setAe({...ae, "sem4":++ae.sem4});break;
                        case "5": setAe({...ae, "sem5":++ae.sem5});break;
                        case "6": setAe({...ae, "sem6":++ae.sem6});break;
                        case "7": setAe({...ae, "sem7":++ae.sem7});break;
                        case "8": setAe({...ae, "sem8":++ae.sem8});break;
                        default:setAe()
                    } 
                }
    
                else if(f.department==='ECE'){
                    setEce({...ece, "total":++ece.total})
                    switch(f.sem){
                        case "1": setEce({...ece, "sem1":++ece.sem1});break;
                        case "2": setEce({...ece, "sem2":++ece.sem2});break;
                        case "3": setEce({...ece, "sem3":++ece.sem3});break;
                        case "4": setEce({...ece, "sem4":++ece.sem4});break;
                        case "5": setEce({...ece, "sem5":++ece.sem5});break;
                        case "6": setEce({...ece, "sem6":++ece.sem6});break;
                        case "7": setEce({...ece, "sem7":++ece.sem7});break;
                        case "8": setEce({...ece, "sem8":++ece.sem8});break;
                        default:setEce();
                    } 
                }
                return null;
            });
        }
    },[students]);
    
  return (
    <div className='divList'>
    <div className='totTextDiv'>
       <h3>Total students:&nbsp;</h3>
       <h2>{students.length}</h2>
    </div>
    <div className='subDivList'>
        <div className='limg1'>
            <div className='divH3'>
                <h3>Branch:&nbsp;</h3> 
               <h2>Computer Science</h2>
            </div>
            <BrachesCard branch={cse} />
        </div>

        <div className='limg2'>
            <div className='divH3'>
                <h3>Branch:&nbsp;</h3> 
               <h2>Information Science</h2>
            </div>
            <BrachesCard branch={ise} />
        </div>
    </div>

    <div className='subDivList'>  
        <div className='limg3'>
            <div className='divH3'>
                <h3>Branch:&nbsp;</h3> 
               <h2>Mechanicle</h2>
            </div>
            <BrachesCard branch={me} />
        </div>
        <div className='limg4'>
             <div className='divH3'>
                <h3>Branch:&nbsp;</h3> 
               <h2>Civil</h2>
            </div>
            <BrachesCard branch={ce} />
        </div>
    </div>

    <div className='subDivList'>
        <div className='limg5'>
            <div className='divH3'>
                <h3>Branch:&nbsp;</h3> 
               <h2>Mechatronics</h2>
            </div>
            <BrachesCard branch={mte} />
        </div>
        <div className='limg6'>
            <div className='divH3'>
                <h3>Branch:&nbsp;</h3> 
               <h2>Aeronotical</h2>
            </div>
            <BrachesCard branch={ae} />
    </div>
    <div className='subDivList'>
        <div className='limg7'>
            <div className='divH3'>
                <h3>Branch:&nbsp;</h3> 
               <h2>ECE</h2>
            </div>
            <BrachesCard branch={ece} />
           </div>
        </div>
    </div>
</div>
  )
}

export default BranchDetails