import React,{useState,useEffect, useContext} from 'react'
import axios from 'axios'
import css from '../fee/fee.module.css'
import imgsrc from '../fee/paidIcon.png'
import { AuthContextApi } from '../contextApi/AuthContextapi';

const UserFeeDetail = () => { 

    const apiKey = process.env.REACT_APP_API_KEY;

    let collegeTotFee=process.env.REACT_APP_COLLEGE_FEE;
    let busTotFee=process.env.REACT_APP_BUS_FEE;
    let activityTotFee=process.env.REACT_APP_ACTIVITY_FEE;
    let esdpTotFee=process.env.REACT_APP_ESDP_FEE;
    let alumniTotFee=process.env.REACT_APP_ALUMNI_FEE;

    const context = useContext(AuthContextApi);
    const {logOut} = context;

    const [studList, setstudList] = useState({
        id:"",
        name:"",
        usn:"",
        department:"",
        sem:"",
        collegefee:"",
        busfee:"",
        activityfee:"",
        esdpfee:"",
        alumnifee:""
    });

    let {name, usn , department, sem,id,collegefee,busfee,activityfee,esdpfee,alumnifee} = studList;

    useEffect(() => {
        let id = localStorage.getItem("ID");
        const accessToken = localStorage.getItem('JWT_ACCESS_KEY');
        
        if(accessToken!==""){
            axios({
                method: 'GET',
                url:`${apiKey}/api/students/get/${id}`,
                headers: {
                    "Authorization": 'Bearer '+ accessToken
                }
            })
            .then((response)=>{
                 setstudList(response.data);
                //  console.log(response.data);
            })
            .catch((error)=>{
                console.log(error);
                logOut();
            });
        }
    }, [])

    return (
        <div className={css.container}>
            <h1 className={css.H1}>{name} - FeeDetails</h1>
            <div className={css.subContainer}>
                <div className={css.subContainer22}>

                        <div className={css.inputMain2}>
                            <div className={css.CardUpper}> 
                                <div className={css.inputSubMain}>
                                    <h1 className={css.H1}>College-Fee</h1>
                                    <div className={css.icon}>
                                   { (collegeTotFee)-collegefee<1 ? <>  <img  src={imgsrc} alt="(fee-paid)" className={css.iconImg} /> </>: ''}
                                    </div>
                                </div>
                                <div className={css.CardMid}>
                                    <div className={css.inputSubMain}>
                                        <label>Total Amount: </label>
                                        <input type="text" value={collegeTotFee+' Rs'} readOnly className={css.input} />
                                    </div>
                                    <div className={css.inputSubMain}>
                                        <label>Amount Paid: </label>
                                        <input type="text" value={collegefee+' Rs'} readOnly className={css.input} />
                                    </div>
                                    <div className={css.inputSubMainRed}>
                                        <label>Amount Pending: </label>
                                        <input type="text" value={collegeTotFee-collegefee+' Rs'} readOnly className={css.input} />
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className={css.inputMain2}>
                            <div className={css.CardUpper}>
                                <div className={css.inputSubMain}>
                                    <h1 className={css.H1}>Bus-Fee</h1>
                                    <div className={css.icon}>
                                   { busTotFee-busfee<1 ? <>  <img alt="" src={imgsrc} className={css.iconImg} /> </>: ''}
                                    </div>
                                </div>
                                <div className={css.CardMid}>
                                    <div className={css.inputSubMain}>
                                        <label>Toatal Amount </label>
                                        <input type="text" value={busTotFee+' Rs'} readOnly className={css.input} />
                                    </div>
                                    <div className={css.inputSubMain}>
                                        <label>Amount Paid: </label>
                                        <input type="text" value={busfee+' Rs'} readOnly className={css.input} />
                                    </div>
                                    <div className={css.inputSubMainRed}>
                                        <label>Amount Pending: </label>
                                        <input type="text" value={busTotFee-busfee+' Rs'} readOnly className={css.input} />
                                    </div>
                                </div>
                            </div>
                            
                        </div>

                        <div className={css.inputMain2}>
                            <div className={css.CardUpper}>
                                <div className={css.inputSubMain}>
                                    <h1 className={css.H1}>Activity-Fee</h1>
                                    <div className={css.icon}>
                                   { activityTotFee-activityfee<1 ? <>  <img alt="" src={imgsrc} className={css.iconImg} /> </>: ''}
                                    </div>
                                </div>
                                <div className={css.CardMid}>
                                    <div className={css.inputSubMain}>
                                        <label>Total Amount: </label>
                                        <input type="text" value={activityTotFee+' Rs'} readOnly className={css.input} />
                                    </div>
                                    <div className={css.inputSubMain}>
                                        <label>Amount Paid: </label>
                                        <input type="text" value={activityfee+' Rs'} readOnly className={css.input} />
                                    </div>
                                    <div className={css.inputSubMainRed}>
                                        <label>Amount Pending: </label>
                                        <input type="text" value={activityTotFee-activityfee+' Rs'} readOnly className={css.input} />
                                    </div>
                                </div>
                            </div>
                        </div>
                </div>
                <div className={css.subContainer33}>

                    <div className={css.inputMain2}>
                        <div className={css.CardUpper} >
                            <div className={css.inputSubMain}>
                                    <h1 className={css.H1}>ESDP-Fee</h1>
                                    <div className={css.icon}>
                                   { esdpTotFee-esdpfee<1 ? <>  <img alt="" src={imgsrc} className={css.iconImg} /> </>: ''}
                                    </div>
                            </div>
                            <div className={css.CardMid}>
                                <div className={css.inputSubMain}>
                                    <label>ESDP-Fee </label>
                                    <input type="text" value={esdpTotFee+' Rs'} readOnly className={css.input} />
                                </div>
                                <div className={css.inputSubMain}>
                                    <label>Amount Paid: </label>
                                    <input type="text" value={esdpfee+' Rs'} readOnly className={css.input} />
                                </div>
                                <div className={css.inputSubMainRed}>
                                    <label>Amount Pending: </label>
                                    <input type="text" value={esdpTotFee-esdpfee+' Rs'} readOnly className={css.input} />
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className={css.inputMain2}>
                        <div className={css.CardUpper} >
                            <div className={css.inputSubMain}>
                                    <h1 className={css.H1}>Alumni-Fee</h1>
                                    <div className={css.icon}>
                                   { alumniTotFee-alumnifee<1 ? <>  <img alt="" src={imgsrc} className={css.iconImg} /> </>: ''}
                                    </div>
                            </div>
                            <div className={css.CardMid}>
                                <div className={css.inputSubMain}>
                                    <label>Alumni-Fee </label>
                                    <input type="text" value={alumniTotFee+' Rs'} readOnly className={css.input} />
                                </div>
                                <div className={css.inputSubMain}>
                                    <label>Amount Paid: </label>
                                    <input type="text" value={alumnifee+' Rs'} readOnly className={css.input} />
                                </div>
                                <div className={css.inputSubMainRed}>
                                    <label>Amount Pending: </label>
                                    <input type="text" value={alumniTotFee-alumnifee+' Rs'} readOnly className={css.input} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default UserFeeDetail;