import React from 'react'
import './scss/branchsCard.scss'

const BrachesCard = ({branch}) => {
  return (
    <div>
        <div className='divH3sub'>
               <h4>Total Branch Students:&nbsp; </h4>
               <h2>{branch.total}</h2>
           </div>
           <div className='divSems' >
               <div className='subdivSems'>
                   <div className='sds'>
                        <h2>{branch.sem1}</h2>
                        <h4>Sem1</h4>
                   </div>
                   <div className='sds'>
                        <h2>{branch.sem4}</h2>
                        <h4>Sem4</h4>
                   </div>
                   <div className='sds'>
                        <h2>{branch.sem7}</h2>
                        <h4>Sem7</h4>
                   </div>
               </div>
               <div>
                   <div className='sds'>
                        <h2>{branch.sem2}</h2>
                        <h4>Sem2</h4>
                   </div>
                   <div className='sds'>
                        <h2>{branch.sem5}</h2>
                        <h4>Sem5</h4>
                   </div>
                   <div className='sds'>
                        <h2>{branch.sem8}</h2>
                        <h4>Sem8</h4>
                   </div>
               </div>
               <div>
                   <div className='sds'>
                        <h2>{branch.sem3}</h2>
                        <h4>Sem3</h4>
                   </div>
                   <div className='sds'>
                        <h2>{branch.sem6}</h2>
                        <h4>Sem6</h4>
                   </div>
               </div>
           </div>
    </div>
  )
}

export default BrachesCard