import React, { createContext, useState, useEffect } from "react";
import axios from "axios";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";

export let AuthContextApi = createContext();

const AuthContextProvider = ({children}) => {

    const loginApiKey = process.env.REACT_APP_ADMIN_LOGIN_API_KEY;
    const refreshApiKey = process.env.REACT_APP_ADMIN_REFRESH_API_KEY;

    const [user, setUser] = useState(1);

    const [roles, setRoles] = useState([]);

    function parseJwt (token) {
      var base64Url = token.split('.')[1];
      var base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      var jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
          return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
      }).join(''));
    //   console.log(JSON.parse(jsonPayload).roles);
      setRoles(JSON.parse(jsonPayload).roles)
      // return JSON.parse(jsonPayload);
    };
    
    useEffect(() => {
        const access_token = localStorage.getItem('JWT_ACCESS_KEY');
        if(access_token!=null){
           parseJwt(access_token);
        }

    }, [user])
    

    useEffect(() => {
        const refresh_token = localStorage.getItem('JWT_REFRESH_KEY');
        if(refresh_token!=null){
            axios({
                method: 'GET',
                url: `${refreshApiKey}/api/auth/refresh`,
                headers: {
                    "Authorization": 'Bearer '+refresh_token
                }
            })
            .then((response)=>{
                localStorage.setItem('JWT_ACCESS_KEY',response.data.accessToken);
                setUser(1);
            })
            .catch((error)=>{
                console.log(error);
                logOut();
            });
        }else{
            logOut();
        }
    }, []);

    const login = (email, password) => { 
        axios({
            method: 'POST',
            url: `${loginApiKey}/api/auth/login`,
            data: {"username":email, "password":password}
        })
        .then((response)=>{
            localStorage.setItem('JWT_REFRESH_KEY',response.data.refreshToken);
            localStorage.setItem('JWT_ACCESS_KEY',response.data.accessToken);
            setUser(1);
        })
        .catch((error)=>{
            toast.error("Something went wrong, Please check your username and password");
            setUser(null)
        })
    }

    const logOut = () => {
        localStorage.clear('JWT_REFRESH_KEY');
        localStorage.clear('JWT_ACCESS_KEY');
        setUser(null);
    }

    return <AuthContextApi.Provider value={{user, login, logOut, roles}} >{children}</AuthContextApi.Provider>
}

export default AuthContextProvider;
